import Endpoint from '@/services/Endpoint'

export default {
  transferFrequencies (payload) {
    return Endpoint().get('/s1/ft/fundsTransferFrequency', {
      params: payload
    })
  },
  createTransferFrequency (payload) {
    return Endpoint().post('/s1/ft/fundsTransferFrequency', payload)
  },
  modifyTransferFrequency (payload) {
    return Endpoint().patch('/s1/ft/fundsTransferFrequency', payload)
  }
}
