<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FREQUENCY_NAME') }}</label>
          <vs-input class="w-full" v-model="transferFrequency.frequencyName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PERIOD') }}</label>
          <div class="flex">
            <div class="flex mr-4 w-full">
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="transferFrequency.periodUomId">
                <vs-select-item v-for="(uom, index) in periodUoms" :key="index" :value="uom.uomId" :text="uom.description"/>
              </vs-select>
            </div>
            <div class="flex">
              <vs-input-number v-model="transferFrequency.period"/>
            </div>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
          <vs-input class="w-full" v-model="transferFrequency.externalFrequencyId"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyTransferFrequency('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyTransferFrequency('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyTransferFrequency('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import FundsTransferFrequencyService from '@/services/ft/FundsTransferFrequencyService'
import UomService from '@/services/commons/UomService'

export default {
  name: 'funds-transfer-frequency-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      transferFrequency: {},
      periodUoms: []
    }
  },
  methods: {
    async load () {
      let self = this
      FundsTransferFrequencyService.transferFrequencies({
        frequencyId: this.$route.params.id
      }).then(function (response) {
        self.transferFrequency = response.data.transferFrequencyList[0] || {}
        self.transferFrequency.disabled = self.transferFrequency.disabled === 'Y'
      })

      UomService.uoms({
        uomTypeEnumId: 'UT_TIME_FREQ_MEASURE',
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.periodUoms = response.data.uomList || []
      })
    },
    modifyTransferFrequency (nextAction) {
      let self = this
      FundsTransferFrequencyService.modifyTransferFrequency({
        frequencyId: this.$route.params.id,
        frequencyName: this.transferFrequency.frequencyName,
        externalFrequencyId: this.transferFrequency.externalFrequencyId,
        periodUomId: this.transferFrequency.periodUomId,
        period: this.transferFrequency.period
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewFundsTransferFrequency', params: { id: response.data.frequencyId } })
          } else {
            self.$router.push({ name: 'BrowseFundsTransferFrequencies' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
